
.testimonials {
  .testimonial-item {
    box-sizing: content-box;
    text-align: center;
    min-height: 32rem;
    .testimonial-img {
      width: 9rem;
      border-radius: 50%;
      margin: 0 auto;
    }
    h3 {
      font-size: 18px;
      font-weight: bold;
      margin: 10px 0 5px 0;
      color: #111;
    }
    h4 {
      font-size: 14px;
      color: #999;
      margin: 0;
    }

    .quote-icon-left,.quote-icon-right {
      color: #c3e8fa;
      font-size: 2.6rem;
    }

    .quote-icon-left {
      display: inline-block;
      left: -5px;
      position: relative;
    }
    .quote-icon-right {
      display: inline-block;
      right: -5px;
      position: relative;
      top: 10px;
    }
    p {
      margin: 0 15px 15px 15px;
      padding: 2rem;
      background: #fff;
      position: relative;
      margin-bottom: 35px;
      border-radius: 6px;
      box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
      &::after {
        content: "";
        width: 0;
        height: 0;
        border-top: 20px solid #fff;
        border-right: 20px solid transparent;
        border-left: 20px solid transparent;
        position: absolute;
        bottom: -20px;
        left: calc(50% - 20px);
      }
    }
    
  }
  .swiper-pagination {
    margin-top: 20px;
    position: relative;
    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background-color: #fff;
      opacity: 1;
      border: 1px solid #149ddd;
    }
    .swiper-pagination-bullet-active {
      background-color: #149ddd;
    }
  }
}
