.services {
  .icon-box {
    margin-bottom: 20px;
  }
  .icon {
    float: left;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 54px;
    height: 54px;
    background: #149ddd;
    border-radius: 50%;
    transition: 0.5s;
    border: 1px solid #149ddd;
    i {
      color: #fff;
      font-size: 24px;
      line-height: 0;
    }
  }

  .title {
    margin-left: 80px;
    font-weight: 700;
    margin-bottom: 15px;
    font-size: 18px;
    a {
      color: #343a40;
    }
  }

  .description {
    margin-left: 80px;
    line-height: 24px;
    font-size: 14px;
  }
}
.icon-box:hover .icon {
    background: #fff;
}
  
.icon-box:hover .icon i {
    color: #149ddd;
}
  
.title a:hover {
    color: #149ddd;
}
  
